<template>
    <el-table :data="this.$store.state.data" fit show-header empty-text="暂无文件" style="width: 100%" max-height="830px" @cell-click="showrow">
      <el-table-column type="selection" width="30" class="checkbox" >
      </el-table-column>
      <el-table-column prop="name" label="文件名" >
        <template slot-scope="scope">
        <i class="fa fa-folder fa-2x" aria-hidden="true" style="color: #FFD659" v-if="scope.row.type=='folder'"></i>
        <i class="fa fa-picture-o fa-2x" aria-hidden="true" style="color: #FF7743" v-if="scope.row.type=='image'"></i>
        <i class="fa fa-file-word-o fa-2x" aria-hidden="true" style="color: #4D97FF" v-if="scope.row.type=='document'"></i>
        <i class="fa fa-file-video-o fa-2x" aria-hidden="true" style="color: #8183F1" v-if="scope.row.type=='video'"></i> 
        <span id="filename" >{{ scope.row.name }}</span>
        <!-- <i class="fa fa-share-alt fa-2x" aria-hidden="true" style="color: #4D97FF"></i> -->
      </template>
      </el-table-column>
      <el-table-column prop="size" label="大小" >
      </el-table-column>
      <el-table-column prop="date" label="修改时间" >
      </el-table-column>
    </el-table>
</template>

<script>
export default {
    data() {
        return {
          show: false,
        }
      },
    methods: {
      showrow: function (row) {
        if(row.type == 'folder'){
          this.$store.commit("folderClick",row.name);
          this.$store.state.fileposition.push(row.name);
          console.log(this.$store.state.fileposition)
        }
        console.log(row.type)
      },
    }
}
</script>

<style lang="less">
 #filename {
     position: relative;
     bottom: 4px;
     margin-left: 15px;
     cursor: pointer;
 }
//  .el-table--enable-row-hover .el-table__body tr:hover>
.fa-share-alt:before {
  margin-left: 150px;
}
.el-table .cell {
  text-overflow: clip!important
}
</style>